// components/AuthGuard.js
import { useRouter } from "next/router";
import { useEffect, useState } from "react";
import { useAuth } from "../context/AuthContext";

// ุตูุญุงุช ุบูุฑ ูุญููุฉ
const PUBLIC_ROUTES = ["/", "/403"];

// ๐ ููุงุนุฏ ุงูุญูุงูุฉ ุงูุตุงุฑูุฉ ููู ุตูุญุฉ
const ROUTE_RULES = [
  { pattern: /^\/dashboard$/, roles: ["admin", "ph", "ca"], permissions: ["view_dashboard"] },

  { pattern: /^\/pharmacist$/, roles: ["admin", "ph"], permissions: ["pharmacist_mode"] },
  { pattern: /^\/cashier$/, roles: ["admin", "ca"], permissions: ["cashier_mode"] },

  { pattern: /^\/products$/, roles: ["admin", "ph"], permissions: ["manage_products"] },
  { pattern: /^\/inventory$/, roles: ["admin", "ph"], permissions: ["manage_inventory"] },

  { pattern: /^\/sales$/, roles: ["admin", "ph", "ca"], permissions: ["add_sale"] },
  { pattern: /^\/returns$/, roles: ["admin", "ph", "ca"], permissions: ["return_sale"] },

  { pattern: /^\/shift-report$/, roles: ["admin", "ca"], permissions: ["cashier_shift"] },

  { pattern: /^\/reports$/, roles: ["admin", "ph", "ca"], permissions: ["view_reports"] },
  { pattern: /^\/profit-report$/, roles: ["admin", "ph"], permissions: ["view_reports"] },

  { pattern: /^\/accounts$/, roles: ["admin"], permissions: ["manage_accounts"] },

  { pattern: /^\/users$/, roles: ["admin"], permissions: ["manage_users"] },

  { pattern: /^\/activity$/, roles: ["admin"], permissions: ["view_activity"] },
];

function findRule(pathname) {
  return ROUTE_RULES.find((r) => r.pattern.test(pathname));
}

export default function AuthGuard({ children }) {
  const router = useRouter();
  const { ready, user, hasRole, hasPermission } = useAuth();
  const [allowed, setAllowed] = useState(false);

  useEffect(() => {
    if (!router.isReady || !ready) return;

    const path = router.pathname;

    // ูุณุงุฑ ุนุงู
    if (PUBLIC_ROUTES.includes(path)) {
      setAllowed(true);
      return;
    }

    // ุจุฏูู ูุณุชุฎุฏู โ ุฑุฌูุน ููุตูุญุฉ ุงูุฑุฆูุณูุฉ
    if (!user) {
      router.replace("/");
      return;
    }

    const rule = findRule(path);

    // Strict Mode โ ุฃู ุตูุญุฉ ุบูุฑ ูุนุฑูุฉ โ ููููุน ุงูุฏุฎูู
    if (!rule) {
      router.replace("/403");
      return;
    }

    // ุชุญูู ูู ุงูุฏูุฑ
    const roleOK = hasRole(rule.roles);
    const permOK = hasPermission(rule.permissions);

    if (!roleOK || !permOK) {
      router.replace("/403");
      return;
    }

    setAllowed(true);
  }, [router.pathname, router.isReady, ready, user]);

  if (!ready) {
    return (
      <div dir="rtl" className="flex items-center justify-center min-h-screen text-gray-500">
        ุฌุงุฑู ุงูุชุญูู ูู ุงูุตูุงุญูุงุชโฆ
      </div>
    );
  }

  if (!allowed) return null;

  return children;
}













